            <div class="subtitle">为什么要节约粮食？</div>
                <p class="text">粮食是一个国家的根基，粮食安全问题是国家最为关键的大事之一。我国是世界产粮大户，但是我国有着14亿人口需要供养，粮食问题我国一直没放松过。经过袁隆平等一大批粮食工作者和广大农民的努力，今天我们吃上了放心粮食，不用担心饥荒。虽然目前粮食问题不明显，但潜在的危机依然存在，国家取消粮票不到30年，切不可未富先奢！</p>
            <div class="inner-border"></div>

            <div class="subtitle">一：世界粮食减产，我国进口受影响</div>
                <p class="text">受到疫情影响、气候的影响以及蝗灾影响等，例如阿根廷、朝鲜、澳洲、埃塞俄比亚、埃及、中东、巴基斯坦等多国粮食出现减产。目前专家预计世界粮食总产量减产将超过20%，原本世界粮食就不够，很多人会面临饥荒威胁。现在国际粮食紧张，以及出于应对疫情的需要，多国已经禁止粮食出口，这给我国粮食进口产生了一定的影响。</p>

                <div class="inner-image">
                    <img :src="img3">
                </div>
            <div class="inner-border"></div>

            <div class="subtitle">二：国内灾害导致粮食减产，粮农劳作异常辛苦</div>
                <p class="text">对于我国来讲，今年南方水灾是比较严重的，很多地方是颗粒无收的情况。今年早稻受到疫情的影响，人们播种粮食，收获粮食非常不容易，餐桌上的人们需要倍加珍惜粮食。</p>

                <div class="inner-image">
                    <img :src="img2">
                </div>
            <div class="inner-border"></div>

            <div class="subtitle">三：粮食浪费现象严重</div>
                <p class="text">根据以前的数据，国人每年在餐桌上浪费的粮食价值高达2000亿元，被倒掉的食物相当于2亿多人一年的口粮，这还只是粮食，每年浪费的蛋白质在800万吨的样子，相当于2.6亿人一年的所需。勤俭节约原本是我们民族的优良品德，然现在已经远离，需要重新建立起来。在一些居住的小区里，卫生保洁人员从垃圾里发现了被倒掉的整个、整碗的馒头、面条、米饭等。无独有偶，一些食堂、饭店浪费粮食的现象也十分严重。可见，在一些单位和家庭里，人们珍惜粮食的观念已经相当淡薄，这种类似现象让人忧心。</p>

                <div class="inner-image">
                    <img :src="img1">
                </div>
            <div class="inner-border"></div>

            <div class="subtitle">四：粮食浪费成了赚钱”工具“题</div>
                <p class="text">总之，随着社会的发展，我们的生活质量有了很大提高，大部分地区的群众不再为吃而发愁。看着满起来的粮袋子，有些人于是就忘记了过去的苦日子，把“爱惜粮食”的教导也抛在了脑后。尤其在饭店的餐桌上，如果讲究节约，岂不丢自己的面子，让人觉得自己寒酸？其实，这种观念真是大错特错。有报道说，在人均收入名列欧洲第一、生活水平居世界前列的瑞士，精打细算、节约光荣已成为不成文的规定，餐厅决不允许顾客浪费，对于浪费者，要处以罚款。如此富裕的国家还这么节俭，我们有何理由摆阔气搞浪费！</p>
            <div class="inner-border"></div>

            <div class="subtitle">五：总结</div>
                <p class="text">节约粮食，是我们每个公民应尽的义务，而不是说你的生活好了，你浪费得起就可以浪费。浪费是一种可耻的行为。只要存有节约的意识，其实做起来很简单：吃饭时吃 多少盛多少，不扔剩饭菜；在餐馆用餐时点菜要适量，而不应该摆阔气，乱点一气。</p>
                <p class="text">在日常生活中，随处都可以看到浪费粮食的现象。也许你并未意识到自己在浪费，也许你认为浪费这一点点算不了什么，也许你仍然以为我们祖国地大物博……由于人们的生活过得越来越好，“节约”一词在人们的心里已日渐忽视和淡忘了。我国是一个人口大国，种种的浪费现象如果继续下去的话，其后果是很严重的！14亿人口，全世界人口的五分之一！如果每人每天浪费1元，一年全国就浪费了4745亿元；如果每人每月浪费500克粮食，一年全国就浪费了65万吨粮食；如果每人每月浪费1吨水，一年全国就浪费了156亿吨水！积沙成堆，积水成河,如果我们每天都能够节约一点的话，就不会浪费这么多水和食物了。</p>
                <p class="text">事实摆在眼前：我国人口已超过13亿，每年的净增长是1200万人；人均耕地面积1.2亩，是世界人均值1/4；耕地面积正以每年30多万亩的速度减少；全国40%的城市人口消耗的粮食依靠进口。在1981－1995年间，全国共减少了耕地8100万亩，因此减少粮食500亿斤。而且这个减少速度仍然在不断加快。乱占耕地、挖沙、土地质量下降、荒漠化等种种现象在蚕食着耕地。现实绝对不容乐观！</p>
